 .nav-header{
    font-size: 24px;
    color: #555555;
    text-decoration: none;
    position: relative;
    right: calc(-30% + 250px);
    white-space: nowrap;
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
}

 nav {
    background-color: #F8F8F8;
    box-shadow: 1px 1px 4px 0 rgba(0,0,0,.1);
    position: sticky;
    width: 100%;
    z-index: 3;
    letter-spacing: 2px;
    font-size: 14px;
    top: 0;
    left: 0;
    margin: auto;
    color: #555555;
    display: flex;
    justify-content: space-between;
  }
  
  nav ul {
    margin: 0;
    padding: 0;
    list-style: none;
    overflow: hidden;
    background-color: #F8F8F8;
  }
  
  nav li a {
    display: block;
    padding: 20px 20px;
    border-right: 1px solid #F8F8F8;
    text-decoration: none;
    color: #555555;

  }
  
  nav li a:hover,
  nav .menu-btn:hover {
    background-color: #F8F8F8;
  }
  
  nav .nav-header {
    display: block;
    float: left;
    font-size: 24px;
    padding: 10px 20px;
    text-decoration: none;
    color: #555555;
  }
  
  /* menu */
  
  nav .menu {
    clear: both;
    max-height: 0;
    transition: max-height .2s ease-out;
  }
  
  /* menu icon */
  
  nav .menu-icon {
    cursor: pointer;
    display: inline-block;
    padding: 28px 20px;
    position: relative;
    user-select: none;
  }
  
  nav .menu-icon .navicon {
    background: #333;
    display: block;
    height: 2px;
    position: relative;
    transition: background .2s ease-out;
    width: 18px;
  }
  
  nav .menu-icon .navicon:before,
  nav .menu-icon .navicon:after {
    background: #333;
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    transition: all .2s ease-out;
    width: 100%;
  }
  
  nav .menu-icon .navicon:before {
    top: 5px;
  }
  
  nav .menu-icon .navicon:after {
    top: -5px;
  }
  
  /* menu btn */
  
  nav .menu-btn {
    display: none;
  }
  
  nav .menu-btn:checked ~ .menu {
    max-height: 300px;
  }
  
  nav .menu-btn:checked ~ .menu-icon .navicon {
    background: transparent;
  }
  
  nav .menu-btn:checked ~ .menu-icon .navicon:before {
    transform: rotate(-45deg);
  }
  
  nav .menu-btn:checked ~ .menu-icon .navicon:after {
    transform: rotate(45deg);
  }
  
  nav .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
  nav .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
    top: 0;
  }
  
  /* 48em = 768px */
  
  @media (min-width: 55em) {
    nav li {
      float: left;
    }
    nav li a {
      padding: 20px 30px;
    }
    nav .menu {
      clear: none;
      float: right;
      max-height: none;
    }
    nav .menu-icon {
      display: none;
    }
  }
  
  @media (max-width: 800px){
    .nav-header{
        left:0px;
    }
  }

  @media (max-width: 235px) {
    .nav-header {
        width: 10px;
        padding-left: 0;
    }
}

@media (max-width: 350px) {
  .menu {
    padding-top: 40px;
  }
  
}
@media (max-width: 200px) {
  .menu {
    margin-left: -30px;
  }
}